@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400..700&family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

html {
  scroll-behavior: smooth;
}

html, body {
  height: 100%;
  background-color: black; 
  color: white;
  margin: 0; 
}

/* Ensure sections do not overlap */
section {
  margin-top: 6rem; /* Adds space above each section, adjust as needed */
}

h1 { 
  @apply text-4xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-orange-400 to-pink-600 inline-block; 
  font-family: 'Dancing Script', cursive;
}

h2{
  @apply text-4xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-orange-400 to-pink-600 inline-block;
  font-family:'Dancing Script', cursive;
}

.my-custom-class {
  @apply text-pink-800; 
}

.bg-primary-color {
  @apply px-1 py-2 w-full rounded-xl mr-4 bg-gradient-to-r from-orange-500 to-pink-500 text-black font-bold hover:border-none; 
}

/* Adjust main container for full screen sections and prevent overlap */
#Contact {
  min-height: 100vh; /* Ensure contact form section takes full screen height */
  padding-top: 6rem; /* Adjust as needed to ensure space above the form */
}

